  border-color: transparent;
}

.white-color .btn-transparent:hover {
  color: #fff;
}

.dark-color.btn.btn-transparent:focus {
  color: #1d293f!important;
}

.btn-white, 
.white-color .btn-white {
  color: #1d293f;
  background-color: #fff;
  border-color: #fff!important;
